import React from 'react'
import {connect} from 'react-redux'
import UserIcon from 'arui-feather/icon/entity/person'
import SettingsIcon from 'arui-feather/icon/action/settings'
import LogoutIcon from 'arui-feather/icon/action/logout'
import {NavLink} from 'react-router-dom'
import User from 'arui-private/user';

const DashboardHeader = ({username="Загрузка", className}) => {
  
  return (
    <div className={`dashboard-header ${className}`}>
      <div className="d-flex align-items-center justify-content-end flex-nowrap">
        <User text={username} icon={<UserIcon size="m"/>} className="mr-4"/>
        <NavLink to="/dashboard/settings" className="mr-4" title="Настройки">
          <SettingsIcon size="m"/>
        </NavLink>
        <NavLink to="/logout" title="Выход">
          <LogoutIcon size="m"/>
        </NavLink>
      </div>
    </div>
  )
};

export default connect(
  state => ({
    username: state.user.user_full_name
  })
)(DashboardHeader);
