import React from "react";
import Form from "arui-feather/form";
import FormField from "arui-feather/form-field";
import Label from "arui-feather/label";
import {Switch} from "element-react";
import Heading from "arui-feather/heading";
import Paragraph from "arui-feather/paragraph";
import Button from "arui-feather/button";
import addCompanyService from "../../../_services/addCompany.service";
import './SendDocumentsForm.css';
import RepeatIcon from 'arui-feather/icon/action/repeat';
import Spin from "arui-feather/spin";
import CheckBox from 'arui-feather/checkbox'

class SendDocumentsForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      status: 'clean',
      fields: {
        isGBD: false,
        type: 'single',
        frontPage: {
          file: undefined,
          src: '',
        },
        backPage: {
          file: undefined,
          src: '',
        },
      },
      err: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  render() {
    const {status, fields, err} = this.state;
    return (
      <Form onSubmit={this.handleSubmit}>
        <FormField>
          <Label size="s" className="mr-3">Деятельность осуществляется по типовому уставу?</Label>
          <Switch value={true}
                  onText="Да"
                  onColor="rgba(11, 31, 53, 0.9)"
                  offText="Нет"
          />
        </FormField>
        <div className={`bordered ${status}`}>
          <FormField>
            <Heading size="s" className="bordered-heading">Документы</Heading>
            <Paragraph className="mb-0">
              Для формирования документов на подписание, загрузите основные документы:
            </Paragraph>
            <FormField className="mt-3 pb-0">
              <CheckBox checked={fields.type === 'full'}
                        onChange={(v) => this.handleChange('type', v ? 'full' : 'single') }
                        text="Обе стороны удостоверения личности находятся в одном файле"
              />
              {/*<Label size="s" className="mr-3">Обе стороны удостоверения личности находятся в одном файле</Label>*/}
              {/*<Switch value={fields.type}*/}
                      {/*onText="Да"*/}
                      {/*onColor="rgba(11, 31, 53, 0.9)"*/}
                      {/*offText="Нет"*/}
                      {/*onValue="full"*/}
                      {/*offValue="single"*/}
                      {/*onChange={(v) => this.handleChange('type', v)}*/}
              {/*/>*/}
            </FormField>
            { fields.type==='full' && (
              <ul className="row file-list">
                <li className="mb-2 px-3 col-12">
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Изображение имеющее обе стороны удостоверения личности</Label>
                    <input type="file" id="file-1" name="uploadingFiles" accept="image/png, image/jpeg"  onChange={e => this.handleFileChange('frontPage', e)} style={{display: fields.frontPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-1">
                      {fields.frontPage.src && <div className="image-preview mt-2"><img src={fields.frontPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>
              </ul>
            )}
            { fields.type==='single' && (
              <ul className="row file-list">
                <li className="mb-2 px-3 col-md-6">
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Фотография лицевой части уд. личности</Label>
                    <input type="file" id="file-1" name="uploadingFiles" accept="image/png, image/jpeg"  onChange={e => this.handleFileChange('frontPage', e)} style={{display: fields.frontPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-1">
                      {fields.frontPage.src && <div className="image-preview mt-2"><img src={fields.frontPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>
                <li className="mb-2 px-3 col-md-6">
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Фотография тыльной части уд. личности</Label>
                    <input type="file" id="file-2" name="uploadingFiles" accept="image/png, image/jpeg" onChange={e => this.handleFileChange('backPage', e)} style={{display: fields.backPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-2">
                      {fields.backPage.src && <div className="image-preview mt-2"><img src={fields.backPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>
              </ul>
            )}
          </FormField>
          { (status==='clean' || status==='error') && <Button type="submit">Загрузить файлы</Button> }
          { status==='loading'  && <Label><Spin visible size="m" className="mr-3"/> Загружаем данные... Пожалуйста подождите, это может занять несколько минут</Label>}
          { status==='error'  && <Label className=" ml-3 color-danger">{err.debugMessage}</Label>}
        </div>
      </Form>
    )
  }
  
  handleChange(field,val) {
    console.log({field, val});
    const newState = {...this.state};
    newState.fields[field]  = val;
    this.setState(newState);
  }
  
  handleFileChange(field, e) {
    const reader = new FileReader();
    const file = e.target.files[0];
    if(!file) return;
    reader.onload = () => {
      this.handleChange(field, {file, src: reader.result});
    };
    reader.readAsDataURL(file);
  }
  
  handleSubmit(e) {
    e.preventDefault();
    const formData = new FormData();
    if (this.state.fields.type === 'full') {
      formData.append('uploadingFiles', this.state.fields.frontPage.file);
      formData.append('type', 'full');
    } else {
      formData.append('uploadingFiles', this.state.fields.frontPage.file);
      formData.append('uploadingFiles', this.state.fields.backPage.file);
      formData.append('type', 'single');
    }
    this.setState({err: null, status: 'loading'});
    addCompanyService.sendFiles(formData).then(res => {
      console.log(res);
      this.setState({status: 'success'});
    }).catch(err => {
      console.error(err);
      if (err.response && err.response.data) err = err.response.data;
      this.setState({err, status: 'error'});
    });
  }
}

export default SendDocumentsForm;
