import React from 'react'
import MenuItem from 'arui-feather/menu-item'
import PersonBasicForm from './PersonBasicForm'
import AddressContactTab from './AddressContactsTab'
import AddDocumentIPForm from "./AddDocumentIPForm";

class AddFounderForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'basic'
    };
    this.handleTabChange = this.handleTabChange.bind(this);
  }

  render() {
    const {selectedTab} = this.state;
    return (
      <div className="pb-5">
        <div className="menu d-flex mb-3">
          <MenuItem className="mr-3" onClick={(e) => this.handleTabChange(e,'basic')} disabled={selectedTab==='basic'}>Основные данные</MenuItem>
          <MenuItem className="mr-3" onClick={(e) => this.handleTabChange(e,'documents')} disabled={selectedTab==='documents'}>Документы</MenuItem>
          <MenuItem className="" onClick={(e) => this.handleTabChange(e,'address_contacts')} disabled={selectedTab==='address_contacts'}>Адреса и контакты</MenuItem>
        </div>
        { selectedTab==='basic' && <PersonBasicForm/> }
        { selectedTab==='documents' && <AddDocumentIPForm/> }
        { selectedTab==='address_contacts' && <AddressContactTab canSubmit/> }
      </div>
    )
  }

  handleTabChange(e, v) {
    e.preventDefault();
    this.setState({selectedTab: v})
  }

}

export default AddFounderForm;
