import React from 'react'
import Form from 'arui-feather/form'
import FormField from 'arui-feather/form-field'
import './CompanyGeneralDataForm.css'
import Label from 'arui-feather/label'
import Button from 'arui-feather/button'
import Heading from "arui-feather/heading";
import Paragraph from "arui-feather/paragraph";

class AccountsForm extends React.Component {
  state = {
    selectedTariff: false,
  };
  
  render() {
    const {selectedTariff} = this.state;
    return (
      <Form className="pb-3">
        <FormField className="bordered">
          <Label className="d-block mb-3">Тарифный план</Label>
          { selectedTariff && (
            <div className="d-flex justify-content-between align-items-center">
              <div className="left">
                <div className="font-bold">
                  <Heading size="s" className="mt-0">"Стандарт"</Heading>
                  <Label size="m" className="ml-2">0 <small className="opacity-middle">KZT</small> / 1 мес.</Label>
                </div>
              </div>
              <div className="right">
                <Button view="extra">
                  Изменить тариф
                </Button>
              </div>
            </div>
          )}
          { !selectedTariff && (
            <div className="d-flex justify-content-between align-items-center">
              <div className="left">
                <div className="font-bold mt-2 opacity-middle">
                  <Paragraph>У вас нет выбранного тарифа, выберите тариф что бы продолжить</Paragraph>
                </div>
              </div>
              <div className="right">
                <Button>
                  Выбрать тариф
                </Button>
              </div>
            </div>
          )}
        </FormField>
        <FormField className="bordered">
          <Label className="d-block mb-3">Текущий счет</Label>
          <div className="d-flex justify-content-between align-items-center">
            <div className="left">
              <div className="border_block px-5">
                <Label>USD</Label>
              </div>
            </div>
            <div className="right">
              <Button view="extra">
                Удалить
              </Button>
            </div>
          </div>
        </FormField>
        <FormField className="text-center pb-0">
          <Button view="action">Добавить счет</Button>
        </FormField>
      </Form>
    )
  }
}

export default AccountsForm;
