import CheckBox from 'arui-feather/checkbox'
import Label from 'arui-feather/label'
import React from 'react'

const ProductBox = (
  {
    text='',
    image=<div/>,
    selected=false,
    onClick=()=>{},
    value=""
  }) => {
  return (
    <div className={`product_box ${selected ? 'selected' : ''}`} onClick={(e) => {onClick(value, e)}}>
      <CheckBox checked={!!selected}/>
      <div className="product_box_image d-flex align-items-center text-center">
        {image}
      </div>
      <Label className="d-block my-3 text-center" size="l">{text}</Label>
    </div>
)};

export default ProductBox;