import React from 'react';
import ApplicationMenu from "arui-private/application-menu";
import ApplicationMenuItem from "arui-private/application-menu-item";
import {withRouter} from "react-router-dom";
import Label from "arui-feather/label";
import {addCompanyTabs} from "../AddOrder/AddCompany";
import ApplicationMenuGroup from "arui-private/application-menu-group";

const DashboardMenu = ({routes=[], ...props}) => {
	
	const activeRoute = props.history.location.pathname;
	
	const routeByRouter = (e) => {
		e.preventDefault();
		const route = e.currentTarget.getAttribute('href');
		if (activeRoute !== route) {
			props.history.push(route);
		}
	};
	
	const navs = [...addCompanyTabs];
	
	return (
		<ApplicationMenu theme="alfa-on-color" {...props}>
			{ routes.filter(r => r.onNav).map(
				({path, label, isExact=false, navOptions}) =>
					<ApplicationMenuItem
						checked={((activeRoute.indexOf(path) === 0 && !isExact) || (activeRoute === path && isExact)) && activeRoute.indexOf('dashboard/add/company') === -1}
						onClick={routeByRouter}
						url={path} key={path}
						{...navOptions}
					>
						<Label className="mr-auto">{label}</Label>
					</ApplicationMenuItem>
			)}
			{
				activeRoute.indexOf('dashboard/add/company') !== -1 &&
				<ApplicationMenuGroup title="Новая компания" isExpanded checked>
					{navs.map(({label, url}) => (
						<ApplicationMenuItem onClick={routeByRouter} key={url} isSubItem url={url} checked={activeRoute === url}>{label}</ApplicationMenuItem>
					))}
				</ApplicationMenuGroup>
			}
		</ApplicationMenu>
	)
};


export default withRouter(DashboardMenu);
