import React from 'react'
import MenuItem from 'arui-feather/menu-item'
import CompanyAddressForm from './CompanyAddressForm'
import CompanyContactsTable from './CompanyContactsTable'
import Button from 'arui-feather/button'

class AddressContactTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'address'
    };
    this.handleTabChange = this.handleTabChange.bind(this);
  }

  render() {
    const {selectedTab} = this.state;
    const {canSubmit} = this.props;
    return (
      <div className="pb-5">
        <div className="menu d-flex mb-3">
          <MenuItem className={`mr-3 ${selectedTab==='address' ? "" : 'opacity-middle'}`} onClick={(e) => this.handleTabChange(e,'address')} view={selectedTab==='address' ? 'underlined' : 'plain'}>Адрес</MenuItem>
          <MenuItem className={selectedTab==='contacts' ? "" : 'opacity-middle'} onClick={(e) => this.handleTabChange(e,'contacts')} view={ selectedTab==='contacts' ? 'underlined' : 'plain'}>Контакты</MenuItem>
        </div>
        { selectedTab==='address' && <CompanyAddressForm/> }
        { selectedTab==='contacts' && <CompanyContactsTable/> }
        {canSubmit && <div className="d-flex justify-content-end mt-5">
          <Button view="action" type="button">Отменить</Button>
          <Button view="extra" type="submit">Далее</Button>
        </div>
        }
      </div>
    )
  }

  handleTabChange(e, v) {
    e.preventDefault();
    this.setState({selectedTab: v})
  }

}

export default AddressContactTab;
