import React from 'react';
import Heading from "arui-feather/heading";
import Form from "arui-feather/form";
import FormField from "arui-feather/form-field";
import RadioGroup from "arui-feather/radio-group";
import Radio from "arui-feather/radio";
import Input from "arui-feather/input";
import SearchIcon from 'arui-feather/icon/action/search';
import {isBin, isRequired} from "../../../_helpers/validators";
import Select from "arui-feather/select";
import {YMaps, Map, Placemark} from 'react-yandex-maps';
import Spin from "arui-feather/spin";

const defaultFlags = {
  touched: false,
  dirty: false,
  valid: false,
  changed: false,
  focused: false
};

const clientType = 'clientType';
const bin_iin = 'bin_iin';
const companyName = 'companyName';
const city = 'city';
const branch = 'branch';

const cities = [
  {value: 'Astana', text: 'Нур-Султан'},
  {value: 'Almaty', text: 'Алматы'},
  {value: 'Aqtobe', text: 'Актобе'},
  {value: 'Aqtay', text: 'Актау'},
  {value: 'Aturay', text: 'Атырау'},
  {value: 'Kostanay', text: 'Костанай'},
];
const branches = {
  'Astana': [
    {value: '1', text: 'Астана 1'},
    {value: '2', text: 'Астана 2'},
    {value: '3', text: 'Астана 3'},
  ],
  'Almaty': [
    {value: '1', text: 'Алматы 1'},
    {value: '2', text: 'Алматы 2'},
    {value: '3', text: 'Алматы 3'},
  ],
  'Aqtay': [
    {value: '1', text: 'Актау 1'},
    {value: '2', text: 'Актау 2'},
    {value: '3', text: 'Актау 3'},
  ],
  'Aqtobe': [
    {value: '1', text: 'Актобе 1'},
    {value: '2', text: 'Актобе 2'},
    {value: '3', text: 'Актобе 3'},
  ],
  'Aturay': [
    {value: '1', text: 'Атырау 1'},
    {value: '2', text: 'Атырау 2'},
    {value: '3', text: 'Атырау 3'},
  ],
  'Kostanay': [
    {value: '1', text: 'Костанай 1'},
    {value: '2', text: 'Костанай 2'},
    {value: '3', text: 'Костанай 3'},
  ],
};
const branchNames = {
  '1': {
    name: 'г. Алматы, ул. Чайковского, 149',
    coordinates: [43.242614, 76.939330]
  },
  '2': {
    name: 'Нур-Султан (Астана), просп. Кабанбай Батыра, 13/2',
    coordinates: [51.137041, 71.414861]
  },
  '3': {
    name: 'г. Актобе, просп. Алии Молдагуловой, 5А',
    coordinates: [50.293639, 57.174157]
  },
};



class GetCompanyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        [clientType]: {
          validators: [],
          value:"ИП"
        },
        [bin_iin]: {
          value: "",
          validators: [isRequired, isBin],
        },
        [companyName]: {
          value: "",
          validators: [],
        },
        [city]: {
          value: [],
          validators: [isRequired]
        },
        [branch]: {
          value: [],
          validators: [isRequired]
        }
      },
      mapStatus: 'clean'
    };
    Object.keys(this.state.fields).map(field => {
      this.state.fields[field].flags = {...defaultFlags};
      return field;
    });
    
    this.handleBlur = this.handleBlur.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  
  componentDidMount() {
    this.setState({
      mapStatus: 'loading'
    });
  }
  
  render() {
    const {fields, mapStatus} = this.state;
    return (
      <Form className="row pb-0">
        <div className="col-md-6">
          <Heading size="s">Информация о вашей компании</Heading>
          <FormField>
            <RadioGroup label='Тип клиента'
                        onChange={(v) => this.handleChange(clientType, v)}
                        onFocus={() => this.handleFocus(clientType)}
                        onBlur={() => this.handleBlur(clientType)}
                        value={fields[clientType].value}
                        size="l"
                        type='button'>
              {['ИП', 'ЮЛ'].map(text => (
                  <Radio key={ text }
                         size="s"
                         text={text}
                         type='button'
                         value={text}/>
               ))}
            </RadioGroup>
          </FormField>
          <FormField>
            <Input label="ИИН/БИН"
                   width="available"
                   className="input-required"
                   mask="### ### ### ###"
                   value={fields[bin_iin].value}
                   onChange={(v) => this.handleChange(bin_iin, v)}
                   onFocus={() => this.handleFocus(bin_iin)}
                   onBlur={() => this.handleBlur(bin_iin)}
                   leftAddons={(fields[bin_iin].flags.focused || fields[bin_iin].value) && <SearchIcon size="s"/>}/>
          </FormField>
          <FormField>
            <Input label={fields[clientType].value==='ЮЛ' ? 'Наименование организации' : 'Наименование ИП'}
                   width="available"
                   value={fields[companyName].value}
                   onChange={(v) => this.handleChange(companyName, v)}
                   onFocus={() => this.handleFocus(companyName)}
                   onBlur={() => this.handleBlur(companyName)}/>
          </FormField>
        </div>
        <div className="col-md-6">
          <Heading size="s">Информация о месте обслуживания</Heading>
          <div className="row">
            <div className="col-sm-6">
              <FormField>
                <Select label="Город"
                        width="available"
                        mode="radio"
                        options={cities}
                        value={fields[city].value}
                        onChange={(v) => this.handleChange(city, v)}
                        onFocus={() => this.handleFocus(city)}
                        onBlur={() => this.handleBlur(city)}/>
              </FormField>
            </div>
            <div className="col-sm-6">
              <FormField>
                <Select label="Филиал"
                        width="available"
                        mode="radio"
                        options={branches[fields[city].value]}
                        value={fields[branch].value}
                        onChange={(v) => this.handleChange(branch, v)}
                        onFocus={() => this.handleFocus(branch)}
                        onBlur={() => this.handleBlur(branch)}/>
              </FormField>
            </div>
          </div>
          <FormField>
            <Input label="Адрес филиала"
                   width="available"
                   className="input_disabled-preview"
                   disabled
                   value={branchNames[fields[branch].value] ? branchNames[fields[branch].value].name : ''}/>
          </FormField>
          {
            !!fields[branch].value.length && mapStatus==='loading' && <Spin visible/>
          }
          {
            !!fields[branch].value.length && (
              <YMaps version="2.1">
                <Map width="100%" height="300px" defaultState={{ center: branchNames[fields[branch].value].coordinates, zoom: 13 }} onLoad={() => this.setState({mapStatus: 'success'})}>
                  <Placemark geometry={branchNames[fields[branch].value].coordinates} />
                </Map>
              </YMaps>
            )
          }
        </div>
      </Form>
    )
  }
  
  handleFocus(field) {
    const newState = {...this.state};
    newState.fields[field].flags.focused = true;
    this.setState(newState);
  }
  handleBlur(field) {
    const newState = {...this.state};
    newState.fields[field].flags.focused = false;
    newState.fields[field].flags.touched = true;
    this.setState(newState);
  }
  handleChange(field, val) {
    const newState = {...this.state};
    newState.fields[field].flags.dirty = true;
    newState.fields[field].value  = val;
    this.setState(newState);
  }
  
}

export default GetCompanyForm;
