import React from 'react';
import {Redirect, Route, Switch} from "react-router-dom";
import Button from "arui-feather/button";
import {Progress} from "element-react";
import Label from "arui-feather/label";
import GetCompanyForm from './Forms/GetCompanyForm'
import SendDocumentsForm from './Forms/SendDocumentsForm'
import CompanyGeneralDataForm from './Forms/CompanyGeneralDataForm'
import CompanyDocumentsTable from './Forms/CompanyDocumentsTable'
import AddressContactTab from './Forms/AddressContactsTab'
import FoundersTable from './Forms/FoundersTable'
import AuthorizedPersonsTable from './Forms/AuthorizedPersonsTable'
import ProductsForm from './Forms/ProductsForm'
import AccountsForm from './Forms/AccountsForm'
import Heading from "arui-feather/heading";


export const addCompanyTabs = [
  {url: '/dashboard/add/company/company',   label: 'Компания', component: GetCompanyForm},
  {url: '/dashboard/add/company/base',  label: 'Основные данные', component: CompanyGeneralDataForm},
  {url: '/dashboard/add/company/documents',  label: 'Документы', component: CompanyDocumentsTable},
  {url: '/dashboard/add/company/contacts',  label: 'Адреса и контакты', component: AddressContactTab},
  {url: '/dashboard/add/company/founders',  label: 'Учредители', component: FoundersTable},
  {url: '/dashboard/add/company/authorized-persons',  label: 'Уполномоченные лица', component: AuthorizedPersonsTable},
  {url: '/dashboard/add/company/products',    label: 'Продукты', component: ProductsForm},
  {url: '/dashboard/add/company/accounts', label: 'Текущие счета', component: AccountsForm},
  {url: '/dashboard/add/company/attachments', label: 'Вложения', component: SendDocumentsForm},
];

function AddCompany(props) {
  
  const tabs = [...addCompanyTabs];
  
  const activeRoute = props.history.location.pathname;
  const activeIndex = tabs.findIndex(tab => tab.url === activeRoute);
  const activeHeading = tabs.find(tab => tab.url === activeRoute);
  
  const openNextPage = () => {
    props.history.push(addCompanyTabs[activeIndex + 1].url);
  };
  
  return (
    <div className="add-company-page">
      <div className="progress-bar mt-2  mb-4">
        <Label size="s" className="d-inline-block opacity-high ml-2 mb-1">Анкета заполнена на</Label>
        <Progress strokeWidth={18} percentage={Math.floor(12.5 * activeIndex)} status="exception" textInside />
      </div>
      { activeHeading && <Heading size="l" className="mt-5 mb-4">{activeHeading.label}</Heading>}
      <div className="tab-content">
        <Switch>
          { tabs.map((tab, index) => (
              <Route path={tab.url} component={tab.component ? tab.component : () => <div>No content</div>} key={'route_' + index}/>
          )) }
          <Redirect from="/dashboard/add/company" to={tabs[0].url}/>
        </Switch>
        <div className="text-right mt-5">
          { activeIndex !== addCompanyTabs.length-1 && <Button view="extra" onClick={openNextPage}>Далее</Button> }
          { activeIndex === addCompanyTabs.length-1 && <Button view="extra">Завершить</Button> }
        </div>
      </div>
    </div>
  )
}

export default AddCompany;
