import React from 'react'
import Heading from 'arui-feather/heading'
import AddressForm from './AddressForm'
import FormField from "arui-feather/form-field";
import Radio from "arui-feather/radio";
import RadioGroup from "arui-feather/radio-group";
import Label from "arui-feather/label";

class CompanyAddressForm extends React.Component {
  state = {
    similar: 'yes',
  };
  
  render() {
    const {similar} = this.state;
    return (
      <div className="row">
        <div className="col-12">
          <FormField className="mt-4 pb-1">
            <Label className="mr-3">Адрес ведения бизнеса совпадает с юридическим адресом?</Label>
            <RadioGroup onChange={(v) => this.setState({similar: v})}
                        value={similar}
                        size="l"
                        type='button'>
              {[{value: 'yes', text:'Да'}, {value: 'no', text:'Нет'}].map(({value, text}) => (
                <Radio key={ text }
                       size="s"
                       text={text}
                       type='button'
                       value={value}/>
              ))}
            </RadioGroup>
          </FormField>
        </div>
        <div className="col-md-6 pr-md-5">
          <Heading size="s">Юридический адрес</Heading>
          <AddressForm/>
        </div>
        <div className="col-md-6 pr-md-5">
          <Heading size="s">Фактический адрес</Heading>
          <AddressForm/>
        </div>
      </div>
    )
  }
}

export default CompanyAddressForm;
