import React from 'react'
import Heading from 'arui-feather/heading'
import IconCurrent from 'arui-feather/icon/banking/account-default'
// import IconCard from 'arui-feather/icon/banking/card'
import ProductBox from '../../../components/ProductBox'

class ProductsForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      products: {
        current_account: true,
        card: false,
      },
    };
    this.handleProductClick = this.handleProductClick.bind(this);
  }

  render() {
    const {products} = this.state;
    return (
      <div>
        <Heading className="mt-2" size="s">Выберите какие услуги вы хотите подключить</Heading>
        <div className="row">
          <div className="col-md-3">
            <ProductBox text="Текущий счет"
                        image={<IconCurrent className="mx-auto"/>}
                        value="current_account"
                        selected={products.current_account}
                        onClick={this.handleProductClick}
            />
          </div>
          {/*<div className="col-md-3">*/}
            {/*<ProductBox text="Карта"*/}
                        {/*image={<IconCard className="mx-auto"/>}*/}
                        {/*value="card"*/}
                        {/*selected={products.card}*/}
                        {/*onClick={this.handleProductClick}*/}
            {/*/>*/}
          {/*</div>*/}
        </div>
      </div>
    )
  }

  handleProductClick(v) {
    const newState = {...this.state};
    newState.products[v] = !newState.products[v];
    if (v === 'current_account') {
        newState.products[v] = true;
    }
    this.setState({...newState});
  }


}

export default ProductsForm;
