import React, {lazy} from 'react';
import './DashboardPage.css';
import lifecycle from 'react-pure-lifecycle';
import DashboardMenu from "./DashboardMenu";
import {Redirect, Route, Switch, withRouter} from 'react-router-dom';
import DashboardHeader from './DashboardHeader';
import LazyComponent from '../../components/LazyComponent';
import {fetchUserData} from '../../_store/user';
import Header from "arui-private/header";
import Page from "arui-private/page";
import {connect} from "react-redux";
import Footer from "arui-private/footer";
import Label from "arui-feather/label";
import PlusIcon from "arui-feather/icon/action/add";
const CompanyList  = lazy( () => import('../CompanyList/CompanyList'));
const AddOrder  = lazy( () => import('../AddOrder/AddOrder'));

const DashboardPage = () => {

	const routes = [
		{ path: '/dashboard/orders', onNav: true, label: 'Мои компании', component: CompanyList, access: true, isExact: true },
		{ path: '/dashboard/add', onNav: true, label: 'Добавить', component: AddOrder, access: true, isExact: false, navOptions: {icon: <PlusIcon/>}, },
	];
	const defaultReroute = routes[0].path;
	return (
		<div className="dashboard-body">
			<Page
				header={
					<Header
						menu={<DashboardMenu routes={routes}/>}
						rightContent={<DashboardHeader/>}
					/>
				}
				footer={
					<Footer copyright={<Label size="s" className="opacity-middle">© 2001-2019 Альфа-Банк</Label>}/>
				}
			>
				<div className="dashboard-content container px-4">
					<div className="">
						<Switch>
							{
								routes.filter(r => r.access).map(r => <Route path={r.path} exact={r.isExact} key={r.path} component={LazyComponent(r.component)}/>)
							}
							<Redirect from="/" to={defaultReroute}/>
							<Redirect from="*" to="/404"/>
						</Switch>
					</div>
				</div>
			</Page>
		</div>
	)
};

const mapStateToProps = state => ({
	user: state.user,
	username: state.user.username,
});

const mapDispatchToProps = dispatch => ({
	getUser: username => dispatch(fetchUserData(username, {light: true, forced: true})),
});

const methods = {
	componentDidMount(props) {
		props.getUser();
	}
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(lifecycle(methods)(DashboardPage)));
